@keyframes load {
	from {transform: translate(0, 0)}
	to {transform: translate(390px, 0)}
}

@keyframes loadcenter {
	0% {
		width: 0;
	}
	50% {
		width: 50%;
	}
	100% {
		width: 100%;
	}
}

.load_animation{
	margin: auto;
	height: 2px;
	background-color: var(--hrm_palette-blue-purple);
	animation: loadcenter .65s infinite alternate ease-in-out;
}



.main_chckbx  {
	border-radius: 8px;
	border: 1px solid var(--hrm_palette-blue-purple);
	
	font-size: 1em;
	font-weight: 500;
	font-family: inherit;
	
	cursor: pointer;
	transition: border-color 0.25s;
	outline: none;
	
}


.item_chckbx{
	border-left: 1px solid #a8a8a8;
	padding: 0.6em 1.2em;
	color: var(--hrm_palette-blue-purple);
	background-color: #f1f1f1;
	max-height: 25px;
}

.item_chckbx:nth-child(1){
	border-left: none;
	border-top-left-radius: 8px;
	border-bottom-left-radius: 8px;
}

.item_chckbx:last-child{
	
	border-top-right-radius: 8px;
	border-bottom-right-radius: 8px;
}
  
.item_chckbx:hover{
	color: var(--hrm_palette-blue-purple);
	background-color: var(--hrm_palette-blue-purple_10);
	font-weight: 600;
}


[is-active="true"].item_chckbx{
	color: #fff;
	background-color: var(--hrm_palette-blue-purple);
}



